import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Causa_efeito } from '../../../modelo/Causa_efeito'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'causa_efeito-dados-details',   templateUrl: 'causa_efeito_dadosDetails.component.html' }) export class Causa_efeitoDadosDetailsComponent implements OnInit {    causa_efeito: Causa_efeito;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.causa_efeito.Causa_efeito";                 let id = +params['id'];         this.causa_efeito = new Causa_efeito();         this.causa_efeito.cd_causa_efeito = id;         o.objetoJson = this.causa_efeito;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a causa_efeito. '; }      this.causa_efeito = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


